"use script";

import preloader from "./modules/preloader.js";
import resize from "./services/resize.js";

import {
    changeTitleModal,
    changeNewsTitle,
    updateFilterDisplay,
    changeLoremMailText,
    changeFooterTitle,
    changeTelMail
} from "./services/changeDomElements.js";

import click from "./services/click.js";
import {
    hamburgerClick,
    requestClick
} from "./services/clickFunctions.js";
import scroll from "./services/scroll.js";
import menuScroll from "./modules/menu.js";

import mouseAction from "./services/mouseAction.js";

import {cardNew, cards} from "./services/const.js";

document.addEventListener("DOMContentLoaded", () => {
    preloader();

    changeTitleModal();
    changeNewsTitle();
    updateFilterDisplay('modifikator', cardNew);
    changeLoremMailText();


    click((e) => hamburgerClick(e, 'modifikator'), requestClick);

    resize(changeTitleModal, changeNewsTitle,
           () => updateFilterDisplay('modifikator', cardNew),
           changeLoremMailText, changeFooterTitle, changeTelMail);

    scroll(() => menuScroll('modifikator'));

    mouseAction(cards, 'mouseover', 'add', ".news__cards-card",
                "news__cards-card_active");
    mouseAction(cards, 'mouseout', 'remove', ".news__cards-card",
                "news__cards-card_active");

});
